import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Observable";

import { PolicyModel } from "../models";
import { GetCarModelsOfBrand } from "./mock";
import { of } from "rxjs";
import { GetCarModelsOfBrandApiModel } from "./api";

@Injectable({
	providedIn: "root"
})
export class PolicyService {
	constructor(private http: HttpClient) {}

	GetCarModelsOfBrand(data: GetCarModelsOfBrandApiModel.Request): Observable<PolicyModel[]> {
		// return of(GetCarModelsOfBrand.Result as PolicyModel[]);
		const model = new GetCarModelsOfBrandApiModel.Request(data);
		return this.http
			.get("http://185.208.174.92:2000/policy/GetCarModelsOfBrand", { params: model.getRequestQueryParams() })
			.map((response: any) => response.Result as PolicyModel[]);
	}
}
